form {
    width: 90%;
    max-width: 300px;
    background-color: #EEEEEE;
    padding: 33px 40px;
    display: flex;
    align-content: center;
    flex-direction: column;
    border-radius: 25px;
  }
  
  label {
    text-align: center;
    font-weight: bold;
    font-size: 20px;
    margin-bottom: 26px;
    color: #78787e;
  }
  
