  .TodoForm-buttonContainer {
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
  
  .TodoForm-button {
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    font-weight: 400;
    width: 120px;
    height: 48px;
    border-radius: 10px;
    border: none;
    font-family: 'Montserrat';
  }
  
  .TodoForm-button-add {
    background-color: #0E333C;
    color: #EEEEEE;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
  }
  
  .TodoForm-button-cancel {
    color: #202329;
    background: transparent;
  }

  .textarea::-moz-placeholder {
    color: gainsboro;
  }
  .textarea:-ms-input-placeholder {
    color: gainsboro;
  }
  .textarea::placeholder {
    color: gainsboro;
  }
  .textarea:focus {
    outline: none;
  }